import { _decorator, Component, EventKeyboard, Input, input, instantiate, KeyCode, Prefab } from 'cc';
import { Vector3 } from './Config';
import { Square } from './Square';
const { ccclass, property } = _decorator;

@ccclass('GameStart')
export class GameStart extends Component {

    @property(Prefab)
    squarePrefab: Prefab = null;

    private _keyMap: Map<KeyCode, boolean> = new Map();

    squares: Map<number, Square> = new Map();

    uid: number = 0;

    protected onLoad(): void {
        input.on(Input.EventType.KEY_DOWN, this.keyDown, this);
        input.on(Input.EventType.KEY_UP, this.keyUp, this);
    }

    protected start() {
        let square = instantiate(this.squarePrefab).getComponent(Square);
        square.node.parent = this.node;
        this.squares.set(this.uid, square);
    }

    protected update(deltaTime: number) {

        let v3 = new Vector3();

        if (this._keyMap.get(KeyCode.KEY_A))
            v3.x -= 1;

        if (this._keyMap.get(KeyCode.KEY_S))
            v3.y -= 1;

        if (this._keyMap.get(KeyCode.KEY_D))
            v3.x += 1;

        if (this._keyMap.get(KeyCode.KEY_W))
            v3.x += 1;

        // let body = new GameMessageOperationBody();
        // body.dir = v3;

        // let msg = new GameMessageC2S_Operation();
        // msg.uid = this.uid;
        // msg.body = body;

        // WSClient.instance.send(msg);
        this.squares.get(this.uid).move(v3);
    }


    private keyDown(event: EventKeyboard): void {
        if (this._keyMap.has(event.keyCode)) {
            this._keyMap.set(event.keyCode, true);
        }
    }

    private keyUp(event: EventKeyboard): void {
        if (this._keyMap.has(event.keyCode)) {
            this._keyMap.set(event.keyCode, false);
        }
    }
}


